*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: "Montserrat", sans-serif;
}

.App {
  display: grid;
  grid-template-columns:
    [full-start] repeat(8, [col-start] 1fr [col-end])
    [full-end];
}
