.spinner-container {
  display: inline-block;
  width: 100px;
  height: 100px;
  border: 10px solid rgba(195, 195, 195, 0.6);
  border-radius: 50%;
  border-top-color: #ff7f00;
  animation: spin 1s ease-in-out infinite;
  -webkit-animation: spin 1s ease-in-out infinite; }

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg); } }

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg); } }

.pets-list-container {
  grid-column: content-start / content-end;
  display: flex;
  flex-direction: column;
  padding: 2rem; }
  @media only screen and (max-width: 65.5em) {
    .pets-list-container {
      grid-column: full-start / full-end; } }
  .pets-list-container .pets-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
    grid-gap: 2rem;
    gap: 2rem; }

.pet-card {
  width: 100%;
  height: 30rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer; }
  .pet-card:hover .name-container {
    transform: scaleY(1);
    width: 100%; }
  .pet-card .background-image {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    transition: all 0.2s;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 80%, 80% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 80%, 80% 100%, 0 100%); }
  .pet-card .name-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: #ff7f00;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 30%;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 35%, 80% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 35%, 80% 100%, 0 100%);
    transform: scaleY(0);
    width: 3px;
    transition: transform 0.2s, width 0.4s cubic-bezier(1, 0, 0, 1) 0.2s, background-color 0.1s; }
    .pet-card .name-container h3 {
      font-size: 3rem;
      color: #fff; }

